<template>
  <v-app id="app" 
   >
    <div style='width: 100%; 
          transition: all linear .5s;
          will-change: auto;
          height: 100%;position: absolute; top: 0px;left: 0px; z-index: -1;background: url(bg.jpg) no-repeat center;background-size: cover'
    :style='dynamicBg'>
      
    </div>

    <v-layout row 
      justify-center
      style=''
      fluid
      wrap
       
    >
      <v-flex xs12 sm12 md2  class='navRouter' style='max-height: 80vh !important; overflow: none'  >
        <router ></router>
      </v-flex>
      <v-flex 
        style='min-height: 100vh !important'
        xs10 md8  class='pa-1'>

          <transition  name='fade' mode='out-in'>
            <router-view></router-view>
          </transition>

      </v-flex>
      <v-flex md1 class='' >
        <right-panel> </right-panel>
      </v-flex>



    </v-layout>

  </v-app>
</template>

<script> 
  import Router from '@/components/router.vue'
  import rightPanel from '@/components/rightPanel.vue'

  export default {
    name: 'home',
    components: {
      Router,
      rightPanel
    },

    computed: {
      dynamicBg(){
        if(this.$root.$data.$loggedIn)
        {
          return { filter: 'blur(6px)' }
        }
        else
          return {}
      }
    },

    data(){
      return {
        defaultBg: {
          height: '100%',
          background: '#F5F6F9; background: url(bg.jpg) no-repeat  center;background-size: cover; background-attachment: fixed'
        }
      }
    },

    mounted(){

      console.log(JSON.stringify(this.$route,'',2))
        
      let isLogged = axios({
        baseURL: 'http://34.83.223.211/api/v1/',
        url: '/isLoggedIn'
      })
      .then( (r) => {
        if(r)
        {
          console.log(` this.$loggedIn -> ${this.$loggedIn} AND ${JSON.stringify(r)}  `)
          if(!this.$root.$data.$loggedIn)
          {
            this.$root.$data.$loggedIn = true
            this.$root.$data.$name = r.data.name
            this.$router.push('/clients')
          }
        }
      } )
      .catch( (e) => {
          if(e)
            if(this.$root.$data.$loggedIn)
              this.$root.$data.$loggedIn = false
      }  )

    }
};

</script>


